(function() {
  'use strict';

  angular
    .module('app2')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('paso1', {
        url: '/paso1',
        templateUrl: 'app/paso1/paso1.html',
        controller: 'Paso1Controller',
        controllerAs: 'paso1'
      })
      .state('paso1.opc1', {
        url: '/opc1',
        templateUrl: 'app/paso1/formulario_uno.html'
      })
      .state('paso1.opc2', {
        url: '/opc2',
        templateUrl: 'app/paso1/formulario_dos.html'
      })
      .state('paso1.opc3', {
        url: '/opc3',
        templateUrl: 'app/paso1/formulario_tres.html'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

